	module.exports = {"data":{"site":{"siteMetadata":{"title":"Laura Domine","subtitle":"Physics Ph.D. student","copyright":"Powered by Gatsby. © All rights reserved.","menu":[{"label":"Blog","path":"/"},{"label":"Research","path":"/research"},{"label":"Projects","path":"/projects"},{"label":"About","path":"/about/"},{"label":"Contact","path":"/contact/"}],"author":{"name":"Laura Domine","email":"ldomine@stanford.edu","telegram":"#","twitter":"#","github":"Temigo","rss":"#","vk":"#"}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/posts/gits/","categorySlug":"/categories/sf/"},"frontmatter":{"title":"Ghost In The Shell and Man's Fate: across the abyss","date":"2019-04-11","category":"SF","description":"'Ghost In The Shell' (GITS), in Japanese 攻殻機動隊 (Kōkaku kidōtai) is a famous animation movie directed by Mamoru Oshii that came out in 1995. 'Man's Fate' (1933, 'La condition humaine' in French) is a novel by André Malraux that is no less well-known (at least in France). Both of these works deeply impressed me. After some maturation time, I was astonished to see some parallels emerge and that is what I would like to share with you here."}}},{"node":{"fields":{"slug":"/posts/luminous/","categorySlug":"/categories/sf/"},"frontmatter":{"title":"Luminous","date":"2017-10-29","category":"SF","description":"'Luminous' is a science-fiction short story published by Greg Egan in 1995. Starting as a dark thriller, it leads us very quickly on a trail of surprising ideas about mathematics, physics and what we call 'truth'."}}},{"node":{"fields":{"slug":"/posts/piraha/","categorySlug":"/categories/languages/"},"frontmatter":{"title":"The Pirahã language","date":"2017-10-28","category":"Languages","description":"Human languages share few features that distinguish them from other form of animal communication. One of the most famous is recursion. In any language one can always form an arbitrarily long sentence. Always? Not quite. According to Daniel Everett, up to now the only non-native of the Pirahã Amazonian tribe who has ever understood their language, the Pirahã language does have a sentence of maximal length. Of course this statement stirred a durable controverse among linguists and prompted Noam Chomsky to criticize him as a 'charlatan'. Yet his story is interesting and, in my opinion, worth listening to."}}}]}},"pathContext":{"tag":"Books"}}

